<template>
<div>
    <div class="search">
        <!-- <Search :serversData="datas" @onSubmit="getdata($event)"/> -->
        <Search  @onSubmit="click($event)"/>
    </div>
    <div>
        <bill ref="bill"/>
    </div>
</div>
</template>

<script>
import Search from './SearchItem'
import bill from './bill'
import { getToken, getRole } from '@/utils/auth'
export default {
    components: {
        Search,
        bill
    },
    created() {
        if (getToken() === undefined || getRole() !== 'admin') {
            this.$router.push({ path:  '/login'}).catch(err => console.log(err))
            Message({
                message: '请提供足够权限的用户',
                type: 'error',
                duration: 5 * 100
            })
        }
    },
    methods: {
        click(e) {
                //this.$refs.child.$emit('childMethod','发送给方法一的数据') // 方法1:触发监听事件
                this.$refs.bill.getBillMethod(e) // 方法2:直接调用
            },
    }
}
</script>

