<template>
  <el-form :inline="true" :model="formInline" class="demo-form-inline">
    <el-form-item label="集群:">
      <el-select v-model="formInline.cluster" clearable placeholder="集群名称">
        <el-option v-for="value,index in datas.clusters" :key=index :label=value :value=value></el-option>
      </el-select>
    </el-form-item>
    <!-- <el-form-item label="项目:">
      <el-select  v-model="formInline.project" placeholder="项目名称">
        <el-option v-for="(value,key) in projects" :key=key :label=value :value=value></el-option>
      </el-select>
    </el-form-item> -->
    <el-form-item label="查询日期">
        <el-date-picker
        value-format="yyyy-MM-dd"
        v-model="formInline.date"
        type="date"
        placeholder="选择日期">
        </el-date-picker>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit">账单明细</el-button>
      <!-- <el-button type="primary" @click="checkoutExcel">到出excel</el-button> -->
      <el-button type="primary" @click="createBill" :loading="loading">生产账单</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
// import channelServ from '@/api/channel'
import servers from '@/api/servers'
export default {
  data() {
    return {
      formInline: {
        cluster: '',
        date: ''
      },
      // projects:{
      // },
      datas: {
      },
      loading:false,
    }
  },
  created() {
          new Promise((resolve, reject) => {
            servers.getChannels().then(res => {
                // console.log(res.data.data)
                this.datas=res.data.data;
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
      },
  methods: {
    onSubmit: function() {
        this.$emit("onSubmit",this.formInline);
    },
    // async regionChange(value) {
    //   this.projects = this.datas.projects.filter((item) => item.cluster_name === value)[0].projects_map;
    // },
    createBill() {
      this.loading=true;
      servers.createBill(this.formInline);
      setTimeout(() => {this.loading=false},10000);
    },
    checkoutExcel() {
      console.log("abc")
    }
  }
}
</script>

