import request from '@/utils/request'
import qs from 'qs'

export default {
    getChannels() {
        return request({
            url: '/api/v1/servers/ServerList',
            method: 'get'
        })
    },

    getData(params) {
        let url = '/api/v1/servers/ServerList';
        var pas = qs.stringify(params);
        var resp = url + '?' + pas;
        return request({
            url: resp,
            method: 'get'
        })
    },

    createBill(params) {
        let url = '/api/v1/servers/CreateBill';
        var pas = qs.stringify(params);
        var resp = url + '?' + pas;
        return request({
            url: resp,
            method: 'get'
        })
    },
    getBill(params) {
        let url = '/api/v1/servers/GetBill';
        var pas = qs.stringify(params);
        var resp = url + '?' + pas;
        return request({
            url: resp,
            method: 'get'
        })
    },

    Syncdata() {
        let url = '/api/v1/servers/SyncData'
        return request({
            url: url,
            method: 'get'
        })
    },

    createVNCConsole(params) {
        let url = '/api/v1/servers/CreateVNCConsole'
        return request({
            url: url,
            method: 'post',
            params
        })
    },
}