<template>
    <div>
        <div :style="{display:download}">
            <download-excel
                :data   = "tableData"
                :fields = "json_fields"
                type    = "csv"
                worksheet = "账单"
                name    = "账单.xls">
                <el-button type="success"> 到出excel</el-button>
            </download-excel>
        </div>
        <el-table :data="tableData"  stripe style="width: 100%" >
            <el-table-column fixed  prop="date" label="日期"> </el-table-column>
            <el-table-column fixed  prop="cluster" label="集群"> </el-table-column>
            <el-table-column fixed  prop="project" label="项目"> </el-table-column>
            <el-table-column fixed  prop="servers" label="云主机台数(台)" > </el-table-column>
            <el-table-column fixed  prop="vcpu" label="cpu数量(核)" > </el-table-column>
            <el-table-column fixed  prop="ram" label="内存数量(G)" > </el-table-column>
            <el-table-column fixed  prop="disk" label="硬盘数量(G)" > </el-table-column>
        </el-table>
    </div>
</template>

<script>
    // import channelServ from '@/api/channel'
    import servers from '@/api/servers'
    export default {
        data() {
            return {
                json_fields: {
                    '日期': 'date',
                    '集群': 'cluster',
                    '项目': 'project',
                    '云主机台数(台)': 'servers',
                    'cpu数量(核)': 'vcpu',
                    '内存数量(G)': 'ram',
                    '硬盘数量(G)': 'disk',
                },
                tableData: [],
                download:'none',
            }
        },
        methods: {
            async getBillMethod(e) {
                let qs = {
                    cluster: e.cluster,
                    date: e.date
                };
                new Promise((resolve, reject) => {
                    servers.getBill(qs).then(res => {
                    this.tableData = [];
                    console.log(res.data.data)
                    for ( var data of res.data.data) {
                       var date = data.date;
                       var cluster = data.cluster;
                       for (var vminfo of data.aggdata)
                        this.tableData.push(
                            {
                                date: date,
                                cluster: cluster,
                                project: vminfo._id.project,
                                servers: vminfo.servers,
                                vcpu: vminfo.vcpus,
                                ram: vminfo.ram/1024,
                                disk: vminfo.disk
                            }
                        )
                    } ;
                    this.download = 'block';
                    resolve()
                }).catch(error => {
                    reject(error)
                })
                })
            }
        }

    }
</script>

<style>
    .download{
        display:block;
    }
</style>